




















	var page = 1,
		pagesize = 10,
		callindex 
	export default {
		data() {
			return {
				totalcount: 0,
				totalamount:0,
				list: [
// 					"add_time": "2019-02-15 11:12:33",
// 					"avatar": "http://tat.lpcbd.com/upload/201902/01/201902011551395381.jpg",
// 					"isburying": 0,
// 					"user_name": "183*****716",
// 					"value": 0.46
				]
			};
		},
		onReachBottom() {
            var _self = this;
			if (timer != null) {
				clearTimeout(timer);
			}
			timer = setTimeout(function() {
				_self.load(true);
			}, 1000);
		},
		load(ismore){
			var that = this;
			if (ismore) {
				page++;
			} else {
				that.list = [];
			}
			var data = {
				page: page,
				pagesize: pagesize
			};
			this.$utils.defalsePluginRequest('pl_hb_my_list', callindex, data,
				res => {
					// suc
					if (res.code == 0) {
						this.totalcount = res.data.totalcount;
						this.totalamount = res.data.totalamount;
						if (res.data.list.length > 0) {
							that.list = that.list.concat(res.data.list);
						} else {
							if (ismore) {
								page--
							}
						}
						
					}
				},
				res => {
					// fail
				},
				res => {
					// com
				}
			)
		},
		onLoad(options) {
			callindex = options.callindex;
			var roomid = options.roomid;
			this.load(false)
		},
		computed: {
			phoneByPass() {
				return this.$utils.defaultPlusXing(this.userInfo.username)
			},
			userInfo() {
				return this.$store.getters.getUserInfo
			},
			nickName() {
				return this.userInfo.nick_name.length > 0 ? this.userInfo.nick_name : this.phoneByPass;
			},
		}
	}
